    languageSelected$ = this.angularFireAuth.authState.pipe(
        switchMap((user: any) => this.ngFireService.loadDocument('users', user.uid)),
        map((user: any) => user.language || 'en'),
    );

    constructor(
        private router: Router,
        private angularFireAuth: AngularFireAuth,
        private ngFireService: NgFireService
    ) { }

    setLanguage(value: string) {
        const user = this.angularFireAuth.auth.currentUser;
        this.ngFireService.update('users', user.uid, { language: value })
        .then(_ => this.router.navigate(['app', 'search']));
    }
