import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'app-language-list',
    template: `
        <mat-list>
            <mat-list-item
                *ngFor="let language of this.languages"
                [ngClass]="{ isSelected: this.languageSelected === language.value }"
                (click)="this.setLanguage.emit(language.value)"
            >
                {{ language.lang }}
            </mat-list-item>
        </mat-list>
    `,
    styles: [`
        :host {
            width: 100%;
            position: absolute;
            bottom: 0px;
            top: 120px;
            overflow: auto;
        }
        mat-list-item {
            cursor: pointer;
            border-bottom: 1px solid #ddd;
        }
        .isSelected,
        mat-list-item:hover {
            background: #ddd;
        }
    `]
})
export class LanguageListComponent {
    @Input() languageSelected: string;
    @Input() languages: any[];

    @Output() setLanguage = new EventEmitter();
}
